<template>
  <z-view>
    <z-list slot="extension" :items="items">
      <z-spot
        :distance="60"
        slot-scope="props"
        :index="props.index"
        :image-path="urlImagen(props.image)"
        :to-view="{ name: 'detalle', params: {nombre: props.nombre}}"
      ></z-spot>
    </z-list>
  </z-view>
</template>
<script>
export default {
  data() {
    const items = this.vistas.home
      .filter(x => x.vista == "conocimientos")[0]
      .items.filter(x => x.vista == "herramientas")[0].items;

    return {
      items
    };
  },
  methods: {
    urlImagen(imagen) {
      const url = require("@/assets/conocimientos/herramientas/" + imagen);
      return url;
    }
  }
};
</script>