<template>
  <z-view>
    <div>
      <h1 class="ml2">{{nombre}}</h1>
    </div>
  </z-view>
</template>

<script>
export default {
  computed: {
    nombre() {
      if (this.$zircle.getParams() !== undefined) {
        return this.$zircle.getParams().nombre;
      }
      return "";
    }
  },
  mounted() {
    /* global $ anime*/
    $(".ml2").each(function() {
      $(this).html(
        $(this)
          .text()
          .replace(/(\w)/g, "<span class='letter'>$&</span>")
      );
    });

    anime
      .timeline({ loop: true })
      .add({
        targets: ".ml2 .letter",
        scale: [4, 1],
        opacity: [0, 1],
        translateZ: 0,
        easing: "easeOutExpo",
        duration: 950,
        delay: function(el, i) {
          return 70 * i;
        }
      })
      .add({
        targets: ".ml2",
        opacity: 0,
        duration: 1000,
        easing: "easeOutExpo",
        delay: 1000
      });
  }
};
</script>

<style>
.ml2 {
  font-weight: 900;
  font-size: 1.5em;
}

.ml2 .letter {
  display: inline-block;
  line-height: 1em;
}
</style>
