<template>
  <z-view class="main">
    <p>
      <strong>Información</strong>
    </p>
    <div slot="extension">
      <z-spot
        v-for="(item, index) in items()"
        :key="index"
        :angle="(360 / items().length) * index - angulo * 10"
        :to-view="item.vista"
        :label="item.nombre"
        class="inactive"
      >
        <i class="fa" :class="item.icon"></i>
      </z-spot>
    </div>
  </z-view>
</template>

<script>
import { TweenMax } from "gsap/TweenMaxBase";

export default {
  data() {
    return {
      angulo: 0
    };
  },
  mounted() {
    TweenMax.to(this.$data, 700, {
      angulo: 365,
      repeat: -1
    });
  },
  methods: {
    items() {
      const items = this.vistas.home.filter(x => x.vista == "informacion")[0]
        .items;
      return items;
    }
  }
};
</script>
