<template>
  <z-view class="main" size="xl">
    <!-- <div>
      <img class="circular--square" src="@/assets/SergioRazza.jpg">
    </div>-->
    <div>
      <strong>Sergio Razza</strong>
    </div>

    <div slot="extension">
      <z-spot
        v-for="(item, index) in vistas.home"
        :key="index"
        :angle="(360 / vistas.home.length) * index - angulo * 10"
        :to-view="item.vista"
        :label="item.nombre"
        class="inactive"
        :distance="150"
      >
        <i class="fa" :class="item.icon"></i>
        <div slot="extension">
          <z-spot
            v-for="(item2, index2) in item.items"
            :key="index2"
            size="xxs"
            class="inactive"
            :distance="120"
            :angle="(360 / item.items.length) * index2 - angulo * 50"
          ></z-spot>
        </div>
      </z-spot>
    </div>
  </z-view>
</template>

<script>
//import TweenMax from "gsap/TweenMax";
import { TweenMax } from "gsap/TweenMaxBase";

export default {
  data() {
    return {
      angulo: 0
    };
  },
  methods: {},
  mounted() {
    TweenMax.to(this.$data, 700, {
      angulo: 365,
      repeat: -1
    });
  }
};
</script>

