<template>
  <z-view class="main">
    <p>
      <strong>Experiencia</strong>
    </p>
    <div slot="extension">
      <z-spot
        v-for="(item, index) in items()"
        :key="index"
        :angle="(360 / items().length) * index - angulo * 10"
        :to-view="item.vista"
        :label="item.nombre"
        class="inactive"
        @click.native="mostrarDetalle(item.detalle)"
      >
        <i class="fa" :class="item.icon"></i>
      </z-spot>
    </div>
  </z-view>
</template>

<script>
import { TweenMax } from "gsap/TweenMaxBase";
import "vex-js/dist/css/vex.css";
import "vex-js/dist/css/vex-theme-os.css";

var vex = require("vex-js");
vex.registerPlugin(require("vex-dialog"));
vex.defaultOptions.className = "vex-theme-os";

export default {
  data() {
    return {
      angulo: 0
    };
  },
  mounted() {
    TweenMax.to(this.$data, 700, {
      angulo: 365,
      repeat: -1
    });
  },
  methods: {
    items() {
      const items = this.vistas.home.filter(x => x.vista == "experiencia")[0]
        .items;
      return items;
    },
    mostrarDetalle(detalle) {
      const mensaje = detalle.join("");
      vex.dialog.alert({ unsafeMessage: mensaje });
    }
  }
};
</script>

<style>
.vex.vex-theme-os .vex-content {
  width: 600px;
}
</style>
